:root { --font-ui: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI",
        Roboto, "Helvetica Neue", Arial, sans-serif; }

html, body, input, button, select, textarea {
  font-family: var(--font-ui);
}
:root{
  --ink:#0d2f3a; --accent:#23c75f; --ink-2:#33545d; --bg:#f6f8f9; --pill:#eafff0;
}

/* headings & hero */
.sn-hero h1,
.use-left h3,
.customers h2 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;            /* Bold look */
}

.sn-hero p,
.usecases p,
.faq-a {
  font-family: "Poppins", sans-serif;
  font-weight: 400;            /* Body text */
}


.sn-shell{max-width:1120px;margin:0 auto;padding:0 24px;}
.sn-hero{background:linear-gradient(180deg,#102932,#0b2330); color:#fff; padding:68px 0;}
.sn-hero .grid{display:grid;grid-template-columns:1.05fr .95fr;gap:36px;align-items:center}
.sn-hero h1{font-size:2.4rem;line-height:1.15;margin:0 0 12px}
.sn-hero p{opacity:.9;line-height:1.6}
.sn-cta{display:inline-block;margin-top:18px;padding:10px 16px;border-radius:999px;border:2px solid var(--accent);background:var(--pill);color:#083142;font-weight:700;text-decoration:none}
.sn-card{background:#0f2a37;border-radius:16px;padding:10px;box-shadow:0 18px 36px rgba(0,0,0,.25)}
.sn-card .ph{border-radius:12px;display:block;width:100%}
@media(max-width:900px){.sn-hero .grid{grid-template-columns:1fr}}

.usecases{background:var(--bg);padding:56px 0}
.usecases h2{text-align:center;color:var(--ink);margin:0 0 20px;font-size:2.1rem}
.use-tabwrap{display:flex;justify-content:center}
.use-tabs{display:flex;gap:8px;background:#e8edf0;padding:8px;border-radius:999px;flex-wrap:wrap}
.use-tab{border:0;border-radius:999px;padding:10px 16px;font-weight:700;cursor:pointer;background:transparent;color:#083142}
.use-tab[aria-selected="true"]{background:#0d2f3a;color:#fff}
.use-body{display:grid;grid-template-columns:1.05fr .95fr;gap:32px;align-items:start;margin-top:28px}
.use-left h3{color:var(--ink);font-size:1.9rem;margin:0 0 8px}
.use-left p{color:var(--ink-2);font-size:1.05rem;line-height:1.6;max-width:62ch}
.use-cta{display:inline-block;margin:16px 0;padding:10px 16px;border-radius:999px;text-decoration:none;border:2px solid var(--accent);color:#083142;font-weight:700}
.use-links{margin-top:14px}.use-links a{margin-right:18px;color:#0d2f3a;font-weight:700;text-decoration:none}.use-links a:after{content:" →"}
.use-right .imgcard{border-radius:16px;padding:10px;background:#fff;box-shadow:0 18px 36px rgba(0,0,0,.12)}
.use-right img{width:100%;display:block;border-radius:12px}
.use-panel[hidden]{display:none}
@media(max-width:900px){.use-body{grid-template-columns:1fr}}

.customers{padding:56px 0;background:#fff}
.customers .toprow{display:flex;align-items:center;justify-content:space-between;gap:12px;margin-bottom:18px}
.customers h2{color:var(--ink);font-size:2rem;margin:0}
.pill{padding:10px 16px;border-radius:999px;border:2px solid var(--accent);background:var(--pill);color:#083142;text-decoration:none;font-weight:700}
.track{display:grid;grid-auto-flow:column;grid-auto-columns:80%;gap:16px;overflow-x:auto;scroll-snap-type:x mandatory;padding-bottom:8px}
.card{position:relative;border-radius:16px;overflow:hidden;min-height:220px;background:#d7e1e5;scroll-snap-align:start}
.card .overlay{position:absolute;inset:0;display:flex;align-items:flex-end}
.badge{margin:16px;background:rgba(8,49,66,.85);color:#fff;padding:14px 16px;border-radius:12px;max-width:60%}
.badge b{font-size:1.6rem;display:block}
.car-ctrl{display:flex;justify-content:space-between;align-items:center;margin-top:12px}
.car-btn{border:0;width:44px;height:44px;border-radius:999px;background:#0d2f3a;color:#fff;font-weight:700;cursor:pointer}
.brandrow{display:flex;justify-content:center;gap:28px;margin-top:18px;opacity:.85}
.brandlogo{padding:6px 10px;border-radius:8px;background:#f6f8f9;color:#0d2f3a;font-weight:700}
@media(max-width:900px){.track{grid-auto-columns:92%}}

.faq{background:var(--bg);padding:56px 0}
.faq .sn-shell{max-width:1000px}
.faq-top{display:flex;justify-content:flex-end;margin-bottom:12px}
.faq-collapse{background:none;border:0;color:#0d2f3a;font-weight:700;text-decoration:underline;cursor:pointer}
.faq-list{display:grid;gap:16px}
.faq-item{background:#fff;border-radius:12px;box-shadow:0 8px 18px rgba(0,0,0,.06);border:2px solid #23c75f33}
.faq-q{display:flex;align-items:center;justify-content:space-between;padding:18px 20px;cursor:pointer}
.faq-q h3{margin:0;color:#0b2f3a;font-size:1.4rem}
.chev{width:24px;height:24px;border-radius:6px;border:2px solid #0d2f3a;display:grid;place-items:center;font-weight:700;margin-left:12px}
.faq-a{padding:0 20px 18px;color:#2d4a52;line-height:1.7}
.faq-a a{color:#0d2f3a;font-weight:700}
.faq-item[open] .faq-q{border-bottom:1px solid #e9eef1}
.faq-item[open] .chev{background:#0d2f3a;color:#fff}

/* full-bleed utility (edge-to-edge) */
.sn-shell--full{
  max-width: none;
  width: 65vw;              /* 100vw - 15% */
  margin-inline: auto;      /* center it */
  box-sizing: border-box;   /* include padding in that width */
  padding-inline: clamp(16px, 4vw, 48px);
}

/* full-bleed utility (edge-to-edge) */
.sn-shell--full--timeline{
  max-width: none;
  width: 85vw;              /* 100vw - 15% */
  margin-inline: auto;      /* center it */
  box-sizing: border-box;   /* include padding in that width */
  padding-inline: clamp(16px, 4vw, 48px);
  padding-bottom: 100px;
}

/* brand badge highlight when its slide is active */
.brandlogo.active{background:#0d2f3a;color:#fff;box-shadow:0 8px 16px rgba(0,0,0,.15);transform:translateY(-1px)}


