/* Container */
.ihp {
  padding: clamp(24px, 4vw, 48px);
}

/* Hero */
.ihp__hero {
  max-width: 1400px; /* was 1100px */
  margin: 0 auto clamp(24px, 4vw, 48px);
  h1 {
    margin: 0 0 12px;
    line-height: 1.15;
    font-size: clamp(30px, 3.8vw, 48px); /* a touch larger */
    font-family: var(--primaryFont);
  }
}
.ihp__subtitle { margin: 0 0 16px; }

.ihp__chips { display: flex; flex-wrap: wrap; gap: 8px; }
.ihp__chip {
  padding: 6px 12px;
  border-radius: 999px;
  font-size: 14px;
  background: var(--ihp-chip-bg, rgba(255,255,255,.15));
  color: var(--ihp-text, #fff);
}

/* Journey layout (rows of image + text) */
.ihp__journey {
  max-width: 1400px; /* was 1100px */
  margin: 0 auto;
  display: grid;
  gap: clamp(20px, 2.2vw, 28px); /* slightly wider gutters */
}

.ihp__row {
  display: flex;
  align-items: stretch;
  gap: clamp(20px, 2.2vw, 28px);
}
.ihp__row--reverse { flex-direction: row-reverse; }

/* Media (left/right) */
.ihp__media {
  flex: 1 1 50%;
  position: relative;
  display: grid;
  place-items: center;
  aspect-ratio: 16 / 9;
  min-height: clamp(320px, 32vw, 520px); /* ensures bigger, sharper screenshots */
  background: rgba(255,255,255,.06);
  border-radius: 18px; /* was 16px */
  overflow: hidden;
}
.ihp__media img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
}

/* Media chip */
.ihp__mediaCap {
  position: absolute;
  top: 10px; left: 10px;
  padding: 6px 10px;
  border-radius: 10px;
  background: rgba(0,0,0,.55);
  color: #fff;
  font-size: 13px;
  letter-spacing: .02em;
}

/* Text card (high-contrast surface) */
.ihp__card {
  flex: 1 1 50%;
  border-radius: 18px; /* was 16px */
  background: var(--ihp-card-surface, rgba(0,0,0,.78));
  box-shadow: 0 12px 28px rgba(0,0,0,.2);
  padding: 20px 22px 22px; /* roomier */
  color: var(--ihp-text, #fff);
  display: grid;
  align-content: start;
  gap: 10px;
  overflow: visible;
  font-size: clamp(16px, 1.2vw, 19px); /* bumps bullet/body size */
}

.ihp__card h3 {
  margin: 6px 0 8px;
  font-size: clamp(20px, 2.2vw, 26px); /* larger headings */
  line-height: 1.35;
  word-break: break-word;
  overflow-wrap: anywhere;
}

.ihp__tag { font-size: 13px; text-transform: uppercase; color: var(--ihp-text-dim, rgba(255,255,255,.85)); }

.ihp__bullets {
  margin: 6px 0 10px 20px;
  display: grid;
  gap: 8px; /* a hair more breathing room */
  color: var(--ihp-text-dim, rgba(255,255,255,.85));
}

.ihp__evidence {
  border-top: 1px dashed rgba(255,255,255,.22);
  padding-top: 10px;
  color: var(--ihp-text-dim, rgba(255,255,255,.85));
}
.ihp__evidenceLabel { font-size: 12px; text-transform: uppercase; opacity: .9; }

/* Responsive: stack image above text on small screens */
@media (max-width: 860px) {
  .ihp__row,
  .ihp__row--reverse { flex-direction: column; }
  .ihp__media,
  .ihp__card { flex-basis: auto; }
  .ihp__media { min-height: clamp(220px, 50vw, 340px); }
}

/* === Color & font swaps only (no layout changes) === */

.ihp {
  font-family: var(--font-ui, var(--primaryFont, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif));
}

/* Chips pick up a soft brand tint and border; readable on light/dark */
.ihp__chip {
  background: var(--ihp-chip-bg, rgba(35,108,255,.12));
  color: var(--brand, #236cff);
  border: 1px solid rgba(35,108,255,.22);
  font-weight: 600;
}

/* Section tags use brand color for quick scanning */
.ihp__tag {
  color: var(--brand, #236cff);
  font-weight: 700;
  letter-spacing: .02em;
}

/* Headings/body respect your theme text variables */
.ihp__card h3 {
  color: var(--ihp-text, #0b1f2a);
  font-weight: 700;
}

.ihp__bullets,
.ihp__evidence,
.ihp__evidenceLabel {
  color: var(--ihp-text-dim, rgba(11,31,42,.78));
}

/* Keep media label legible and on-brand */
.ihp__mediaCap {
  background: var(--brand, #236cff);
  color: #fff;
  font-weight: 600;
}

