/* stretch section, but keep nice side padding (matches your shell) */
.sn-shell--full { max-width: none; padding-inline: clamp(16px, 3.4vw, 40px); }

/* header row */
.customersV4 .cv4-header {
  display: flex; align-items: center; justify-content: space-between;
  gap: 16px; margin-bottom: 14px;
}
.customersV4 h2 { margin: 0; font-size: clamp(22px, 2.2vw, 32px); }

/* CTA style borrowed from shell */
.customersV4 .sn-cta.ghost {
  border: 2px solid #2f6f62; color: #2f6f62; background: transparent;
  padding: 8px 14px; border-radius: 999px; font-weight: 600;
}
.customersV4 .sn-cta.ghost:hover { background: #2f6f62; color: #fff; }

/* viewport & track */
.customersV4 .cv4-viewport {
  position: relative; overflow: hidden; width: 100%;
  border-radius: 12px; background: #0e2f3a; /* dark frame, like SN site */
}
.customersV4 .cv4-track {
  display: flex; flex-wrap: nowrap; gap: 0;
  transition: transform 450ms ease; will-change: transform;
}

/* slides occupy the whole viewport width */
.customersV4 .cv4-slide {
  position: relative;
  flex: 0 0 100%;
  max-width: 100%;
  max-height: fit-content;
  aspect-ratio: 16 / 9;          /* keeps heights consistent */
  background: #0e2f3a;
  display: grid; place-items: center;
}
.customersV4 .cv4-slide img {
  display: block; width: 100%; height: 100%; object-fit: contain;
  background: #0e2f3a;
}

/* KPI badges */
.customersV4 .cv4-badge {
  position: absolute;
  bottom: 18px; left: 18px;
  background: rgba(8, 24, 28, 0.9);
  color: #e7f3ef; padding: 12px 16px; border-radius: 12px;
  font-weight: 700; box-shadow: 0 6px 24px rgba(0,0,0,.25);
}
.customersV4 .cv4-badge.right { left: auto; right: 18px; }

/* arrows */
.customersV4 .cv4-arrow {
  position: absolute; top: 50%; transform: translateY(-50%);
  width: 38px; height: 38px; border-radius: 50%;
  border: none; background: #042027; color: #e6f2ee;
  display: grid; place-items: center; font-size: 28px;
  cursor: pointer; box-shadow: 0 4px 14px rgba(0,0,0,.3);
}
.customersV4 .cv4-arrow:hover { background: #083542; }
.customersV4 .cv4-arrow.prev { left: 10px; }
.customersV4 .cv4-arrow.next { right: 10px; }

/* brand pills row */
.customersV4 .cv4-brands {
  display: flex; gap: 18px; justify-content: center; align-items: center;
  padding: 12px 6px; flex-wrap: wrap;
}
.customersV4 .cv4-brandpill {
  padding: 8px 16px; border-radius: 999px;
  border: 2px solid #0e2f3a; background: #e8f3ef; color: #0e2f3a;
  font-weight: 700; cursor: pointer;
}
.customersV4 .cv4-brandpill[aria-current="true"] {
  /* high-contrast inverted active pill as requested earlier */
  background: #0e2f3a; color: #fff; filter: none;
}
.customersV4 .cv4-brandpill:hover { transform: translateY(-1px); }

/* reduce motion preference */
@media (prefers-reduced-motion: reduce) {
  .customersV4 .cv4-track { transition: none; }
}
